import React from 'react';
import {MultiGrid, AutoSizer, CellMeasurer, CellMeasurerCache} from 'react-virtualized';

const rows = Array.from({length: 100}, (v, i) => {
  return {
    sn: i + 1,
    name: 'youmoo',
    born: '2018-01-27',
    age: 25,
    weight: '75kg',
    height: 176,
    addr: '',
    email: 'youmoo@vellichor.me',
    phone: '110',
    hobbies: 'reading'
  }
});
const labels = Object.keys(rows[0]);


export default class extends React.Component {

  constructor(props) {
    super(props);
    ['cellRenderer'].forEach(m => this[m] = this[m].bind(this));

    this._cache = new CellMeasurerCache({
      defaultWidth: 100,
      minWidth: 75,
      fixedHeight: true
    });
  }

  cellRenderer({columnIndex, key, parent, rowIndex, style}) {
    const content = rowIndex === 0 ?
      <span className='title is-5'>{labels[columnIndex]}</span> : rows[rowIndex][labels[columnIndex]];
    return (
      <CellMeasurer
        cache={this._cache}
        columnIndex={columnIndex}
        key={key}
        parent={parent}
        rowIndex={rowIndex}>
        <div
          style={{
            ...style,
            whiteSpace: 'nowrap',
          }}>
          {content}
        </div>
      </CellMeasurer>
    );
  }

  render() {
    return (
      <div className="container" style={{width: '50%'}}>
        <AutoSizer disableHeight>{
          ({width}) => <MultiGrid
            deferredMeasurementCache={this._cache}
            cellRenderer={this.cellRenderer}
            columnWidth={this._cache.columnWidth}
            columnCount={labels.length}
            fixedColumnCount={2}
            fixedRowCount={1}
            height={500}
            rowHeight={40}
            rowCount={rows.length}
            width={width}
          />
        }
        </AutoSizer>
      </div>
    )
  }
}
